.App{
  background-color:rgb(27, 24, 24);
  position: relative;
}
.container
{
  background-color: black;
  width: 100%;
  height: 800px;
}
.row
{
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%,-50%);

}
.col-6 .input
{
  display: flex;
  flex-direction: column;
}
.textfield{
  color: white;
  margin-bottom: 10px;
  background-color: transparent;
  /* color: blue; */
  border: 1px solid white;
  height: 40px;
}
.btn button{
  margin: 20px;
}
.input,.output{
  border-radius: 10px;
  width: 390px;
  border: 1px solid rgb(167, 8, 8);
}
.input
{
  padding: 20px;
}
.output
{
  margin-top: 20px;
  overflow: hidden;
  padding: 10px;
}


/* output */

.result-1{
  background-color: blueviolet;
}
.result-2
{
  background-color: white;
  position: relative;
  height: 250px;
}
.res-val
{
  display: flex;
  flex-direction: row;
  justify-content:space-between;
  margin: 20px;
  flex-wrap: wrap;
}
.output p{
  color: black;
  font-weight: 500;
}
.output h2{
  color: blue;
}
.output span{
  color: red;
  text-shadow: 2px 2px 2px black;
}
